//Colors
$theme_button-color:#8ECC60;
$nav_text-color: #282828;
$nav-associations_text_color: #ffff00;
$first_title_h1: #ff9506;
$carousel_h1-color: #fefefe;
$carousel_h2-color: #fff;
$slider_bg_color: #FFA720;
$carousel_link_color: #74bd34;
$description-first_text_color: #343434;
$description-member_title_color: #010101;
$description-member_select_text_color: #72bc66;
$description-_title_color: #2e2e2e;
$description-meet_theme_megafon: #0D8DC6;
$description-meet_theme_leaf: #3FB72E;
$description-meet_theme_pen: #FFA720;
$description-meet_theme_heart: #DA3348;
$description-meet_theme_house: #191919;
$description-meet_theme_mask: #3D0E7A;
$description-meet_text_color: #4d4d4d;
$calendar-title_text_color: #313131;
$calendar-time_text_color: #40ac17;
$statistic_title_color: #ff9506;
$statistic_text_color: #2a2a2a;
//mixin
@mixin wrap_fon{
  background: #d7d7d7;
  background: -moz-linear-gradient(left, #d7d7d7 0%, #dddddd 12%, #ececec 29%, #fafafa 73%, #ffffff 100%);
  background: -webkit-linear-gradient(left, #d7d7d7 0%,#dddddd 12%,#ececec 29%,#fafafa 73%,#ffffff 100%);
  background: linear-gradient(to right, #d7d7d7 0%,#dddddd 12%,#ececec 29%,#fafafa 73%,#ffffff 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#d7d7d7', endColorstr='#ffffff',GradientType=1 );
}
//Fonts
$OSRegular: "OpenSansRegular";


.clr_ln:after {
  content: '';
  clear: both;
  display: table;
  width: 100%;
}

.wrapper_page{
  overflow: hidden;
}
p{
  margin: 0;
}
a {
  outline-style: none;
  &:focus {
    outline-style: none;
  }
}
.header{
  position: relative;
  z-index: 2;
}
.header-container{
  padding: 19px 277px 21px 0;
  box-shadow: 0px 10px 12px -3px rgba(0, 0, 0, 0.5);
}
.theme_button-donate{
  border: 3px solid $theme_button-color;
  border-radius: 8px;
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.24);
  color: $theme_button-color;
  text-transform: uppercase;
  text-decoration: none;
  transition: all 0.5s;
  display: inline-block;
  &:hover{
    background: $theme_button-color;
    color: #ffffff;
  }
}
.button_donate{
  float: right;
  margin-left: 65px;
  padding: 0 23px;
  font: 25px/56px $OSRegular,Arial,Helvetica,sans-serif;
  font-weight: 400;
}
#nav{
  float: right;
  ul{
    padding: 0;
    margin: 0;
    list-style: none;
    line-height: 0;
    font-size: 0;
    li{
      display: inline-block;
      a{
        display: inline-block;
        font: 16.67px/21px $OSRegular,Arial,Helvetica,sans-serif;
        color: $nav_text-color;
        text-transform: uppercase;
        padding: 20px 26px;
        text-decoration: none;
        &:hover{
          text-decoration: underline;
        }
      }
    }
  }
}
.nav-associations{
  position: absolute;
  top: 0;
  z-index: -1;
  width: 100%;
  text-align: right;
  padding: 0 275px 0 0;
  opacity: 0.85;
  background-color: #40ac17;
  ul{
    padding: 0;
    margin: 0;
    list-style: none;
    line-height: 0;
    font-size: 0;
    -webkit-transition: all 4s;
    -moz-transition: all 4s;
    -ms-transition: all 4s;
    -o-transition: all 4s;
    transition: all 4s;
    li{
      display: inline-block;
      vertical-align: top;
      margin: 0 0 0 45px;
      a{
        display: inline-block;
        font: 17px/67px $OSRegular,Arial,Helvetica,sans-serif;
        color: #ffffff;
        text-decoration: none;
        &:hover{
          color: $nav-associations_text_color;
        }
      }
      .active{
        a{
          color: $nav-associations_text_color;
        }
      }
    }
  }
}
.slider-block{
  position: relative;
}
.slider__poster{
  position: absolute;
  top: 60px;
  z-index: 1;
  max-width: 645px;
}
.slider__poster_theme{
  opacity: 0.7;
  background-color: #ff9506;
  border-radius: 17px;
  font: 28px/32px $OSRegular,Arial,Helvetica,sans-serif;
  color: #ffffff;
  padding: 30px 20px 25px 50px;
  h1{
    font: 28px/32px $OSRegular,Arial,Helvetica,sans-serif;
    color: #ffffff;
    margin: 0 0 30px 0;
  }
  p{
    margin: 0 0 55px 0;
  }
  .poster__button{
    color: #ffffff;
    text-transform: uppercase;
    text-decoration: none;
    font: 28px/32px $OSRegular,Arial,Helvetica,sans-serif;
    border: 3px solid white;
    border-radius: 3px;
    float: right;
    -webkit-transition: all 55ms;
    -moz-transition: all 55ms;
    -ms-transition: all 55ms;
    -o-transition: all 55ms;
    transition: all 55ms;
    &:hover{
      border-color: #FEFD32;
    }
  }
  .poster__button-learn-more_theme{
    padding: 5px 15px;
    margin: 0 0 0 25px;
  }
  .poster__button-donate_theme{
    padding: 5px 25px 5px 20px;
    margin: 0 0 0 25px;
  }
}
.outer{
  max-width: 100%;
  position: relative;
}
#big{
  padding: 0;
  margin: 0;
  list-style: none;
  box-shadow: inset 0px -11px 12px -7px rgba(0,0,0,0.36);
  .item{
    background: none;
    padding: 0;
  }
}
.outer__slider-title-box{
  width: 825px;
  text-align: center;
  position: relative;
  z-index: 2;
  margin: -231px auto 0 auto;
  h1{
    font: 111px/99px $OSRegular,Arial,Helvetica,sans-serif;
    color: $first_title_h1;
    text-transform: uppercase;
    margin:0 0 20px 0;
  }
  div{
    padding: 10px 157px 20px 157px;
    position: relative;
    display: inline-block;
    margin-bottom: 9px;
    &:before{
      content: '';
      position: absolute;
      top: 0; right: 0; bottom: 0; left: 0;
      z-index: -1;
      background: $slider_bg_color;
      border-radius: 12px 12px 0 0;
      transform: perspective(.5em) rotateX(1deg);
    }
    h2{
      font: 67px/71px $OSRegular,Arial,Helvetica,sans-serif;
      margin: 0;
      a{
        color: #ffffff;
        text-decoration: none;
      }
    }
  }
}
#thumbs{
  padding: 60px 94px;
  list-style: none;
  border-bottom: 2px solid #74bd34;
  border-top: 2px solid #74bd34;
  margin: 0;
  box-shadow: 0px 7px 12px -7px rgba(0,0,0,0.36);
  .owl-item{
    //width: auto !important;
    margin: 0 10px;
    h1{
      font: 16.67px/21px $OSRegular,Arial,Helvetica,sans-serif;
      color: $carousel_h1-color;
      margin: 0 0 5px 0;
    }
    h2{
      font: 41.67px/46px $OSRegular,Arial,Helvetica,sans-serif;
      color: $carousel_h2-color;
      text-transform: uppercase;
      margin: 15px 0 15px 0;
      align-self: flex-end;
      text-align: center;
    }
  }
  .current{
    .item{
      background: none;
      height: 360px;
      h1 {
        font: 22.22px/26px $OSRegular, Arial, Helvetica, sans-serif;
      }
      h2 {
        font: 46.35px/49px $OSRegular,Arial,Helvetica,sans-serif;
      }
    }
    .carousel-box-1{
      background-color: #d82648;
    }
    .carousel-box-2{
      background-color: #4eb3ee;
    }
    .carousel-box-3{
      background-color: #ffa720;
    }
    .carousel-box-4{
      background-color: #37a93c;
    }
    .carousel-box-5{
      background-color: #9a6c30;
    }
  }
  .item{
    height: 315px;
    padding: 17px 0 15px 0;
  }
  .carousel-box-1{
    background-color: #d82648;
  }
  .carousel-box-2{
    background-color: #4eb3ee;
  }
  .carousel-box-3{
    background-color: #ffa720;
  }
  .carousel-box-4{
    background-color: #37a93c;
  }
  .carousel-box-5{
    background-color: #9a6c30;
  }
}
.owl-carousel{
  z-index: 0;
  .owl-stage-outer{
    position: static;
  }
  .owl-stage{
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: center;
    position: static;
  }
}
.owl-theme{
    .owl-prev,
    .owl-next{
      position: absolute;
      top: 150px;
      background: none !important;
      font-size: 30px !important;
      i{
        opacity: 0.5;
        color: #74bd34;

      }
    }
  .owl-prev{
    left: 10px;
  }
  .owl-next{
    right: 10px;
  }
}
.outer__link-see-all{
  position: absolute;
  bottom: -54px;
  right: 1px;
  text-align: center;
  box-shadow: 0px 7px 12px -7px rgba(0,0,0,0.36);
  border-bottom: 2px solid #74bd34;
  border-left: 2px solid #74bd34;
  border-radius: 0 0 0 110px;
  background: #fff;
  width: 250px;
  a{
    text-transform: uppercase;
    text-decoration: none;
    font: 31px/54px $OSRegular,Arial,Helvetica,sans-serif;
    color: $carousel_link_color;
  }
}
.wrap_fon{
  @include wrap_fon;
  padding: 80px 0 0 72px;
}
.description-member__box{
  margin: 90px 0 0 0;
  font: 17px/39px $OSRegular,Arial,Helvetica,sans-serif;
  color: $description-first_text_color;
  h1{
    font: 28px/44px $OSRegular,Arial,Helvetica,sans-serif;
    color: $description-member_title_color;
    margin: 0 0 30px 0;
  }
  b{
    font: 39px/44px $OSRegular,Arial,Helvetica,sans-serif;
    color: $description-member_select_text_color;
    margin: 0;
  }
  p{
    margin: 0 0 46px 0;
  }
  a{
    float: right;
    font: 25px/56px $OSRegular,Arial,Helvetica,sans-serif;
    padding: 0 25px 0 30px;
    margin: 0 26px 0 0;
  }
}
.description-meet{
  padding: 100px 215px 105px;
  h1{
    font: 50px/56px $OSRegular,Arial,Helvetica,sans-serif;
    color: $description-_title_color;
    text-align: center;
    margin: 0 0 105px 0;
  }
  ul{
    padding: 0;
    margin: 0;
    list-style: none;
  }
}
.description-meet__box{
  display: inline-block;
  max-width: 490px;
  margin: 0 6px 115px 6px;
}
.meet__box__icon-box{
  width: 100px;
  height: 100px;
  position: relative;
  display: inline-block;
  margin-right: 25px;
  float: left;
  i{
    font-size: 22px;
  }
}
.meet__box__text-box{
  font: 17px/30px $OSRegular,Arial,Helvetica,sans-serif;
  color: $description-meet_text_color;
  h2{
    font: 28px/30px $OSRegular,Arial,Helvetica,sans-serif;
    color: $description-_title_color;
    margin: 0 0 15px 0;
  }
  p{
    margin: 0;
    width: 80%;
  }
}
.theme-box_megaphone{
  border: 3px solid $description-meet_theme_megafon;
  i{
    color: $description-meet_theme_megafon;
    position: absolute;
    top: 14px;
    left: 10px;
  }
}
.theme-box_leaf{
  border: 3px solid $description-meet_theme_leaf;
  i{
    color: $description-meet_theme_leaf;
    position: absolute;
    top: 68px;
    left: 10px;
  }
}
.theme-box_pen{
  border: 3px solid $description-meet_theme_pen;
  i{
    color: $description-meet_theme_pen;
    position: absolute;
    top: 68px;
    left: 9px;
  }
}
.theme-box_heart{
  border: 3px solid $description-meet_theme_heart;
  i{
    position: absolute;
    color: $description-meet_theme_heart;
    top: 65px;
    left: 10px;
  }
}
.theme-box_house{
  border: 3px solid $description-meet_theme_house;
  i{
    position: absolute;
    color: $description-meet_theme_house;
    top: 67px;
    left: 9px;
  }
}
.theme-box_mask{
  border: 3px solid $description-meet_theme_mask;
  i{
    position: absolute;
    color: $description-meet_theme_mask;
    top: 69px;
    left: 9px;
  }
}
.description-life-wrap{
  background-image: url(../img/donate-fon-block.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.description-life{
  padding: 230px 0 270px;
}
.description-life_box{
  position: relative;
  &:before,
  &:after{
    content: '';
    width: 400px;
    height: 1px;
    background: #ffffff;
    position: absolute;
    left: 440px;
  }
  &:before{
    top: -125px;
  }
  &:after{
    bottom: -110px;
  }
}
.description-life_box__theme{
  text-align: center;
  font: 39px/42px $OSRegular,Arial,Helvetica,sans-serif;
  color: #ffffff;
  h1{
    font: 84px/88px $OSRegular,Arial,Helvetica,sans-serif;
    margin: 0 0 40px 0;
    text-transform: uppercase;
  }
  p{
    margin-bottom: 125px;
  }
  .theme_button-donate{
    padding: 0 25px;
    font: 25px/56px $OSRegular,Arial,Helvetica,sans-serif;
  }
}
.calendar-title{
  padding: 125px 0 135px 0;
  text-align: center;
  h1{
    font: 50px/56px $OSRegular,Arial,Helvetica,sans-serif;
    color: $calendar-title_text_color;
    text-transform: uppercase;
    margin: 0;
  }
}
.calendar-container{
  position: relative;
  height: 100%;
  &:before,
  &:after{
    content: ' ';
    position: absolute;
    height: 14px;
    background: rgba(116, 189, 52, 0.75);
    right: 0;
    left: 0;
  }
  &:before{
    top: 0;
  }
  &:after{
    bottom: 0;
  }
  img{
    margin: 61px 65px 88px 125px;
  }
  background-image: url(../img/calendar-fon.png);
  background-position: right;
  background-size: contain;
  background-repeat: no-repeat;
}
.calendar__content{
  display: inline-block;
  max-width: 520px;
  margin: 55px 0 0 0;
  a{
    float: right;
  }
}
.calendar__content_theme{
  font: 17px/39px $OSRegular,Arial,Helvetica,sans-serif;
  color: $description-first_text_color;
  time{
    font: 17px/39px $OSRegular,Arial,Helvetica,sans-serif;
    color: $calendar-time_text_color;
    margin: 0 0 35px 0;
  }
  h2{
    font: 28px/56px $OSRegular,Arial,Helvetica,sans-serif;
    color: $description-_title_color;
    margin: 0 0 10px 0;
  }
  p{
    margin: 0 0 25px 0;
  }
  .theme_button-donate{
    font: 25px/56px $OSRegular,Arial,Helvetica,sans-serif;
    padding:0 25px;
  }
}
.statistic-wrap{
  background-image: url(../img/statistic-fon.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  padding: 200px 0 270px 0;
}
.statistic-box{
  text-align: center;
}
.statistic_theme{
  font: 33px/37px $OSRegular,Arial,Helvetica,sans-serif;
  color: $statistic_text_color;
  h1{
    font: 83px/63px $OSRegular,Arial,Helvetica,sans-serif;
    color: $statistic_title_color;
    margin: 0 0 65px 0;
  }
}
.box-content{
  max-width: 210px;
  margin: 0 auto;
}
.form-block{
  background: #91DA5E;
}
.form-block__box-content{
  background-image: url(../img/form-img-fon.png);
  background-position: left;
  background-size: contain;
  background-repeat: no-repeat;
  border: 0 solid;
  border-radius: 0 50% 50% 0;
  padding: 140px 80px 152px 700px;
}
.box-content_theme{
  h1{
    font: 103px/107px $OSRegular,Arial,Helvetica,sans-serif;
    color: #ffffff;
    margin: 0;
  }
  span{
    font: 103px/107px $OSRegular,Arial,Helvetica,sans-serif;
    text-transform: uppercase;
    color: #ff3737;
  }
}
#form{
  padding: 135px 0 0 100px;
  input[type="text"]{
    width: 615px !important;
    height: 78px !important;
    margin: 0 0 25px 0;
  }
  textarea{
    width: 615px !important;
    height: 245px !important;
    margin: 0 0 38px 0;
  }
  input[type="text"],
  textarea{
    padding: 21px 10px 24px 17px;
  }
  input[type="submit"]{
    padding: 0 35px;
    text-align: center;
    margin: 0 0 0 255px;
  }
}
.form_theme{
  input[type="text"],
  textarea{
    font: 30px/34px $OSRegular,Arial,Helvetica,sans-serif;
    border: 0 solid;
    border-radius: 15px;
    display: block;
  }
  input[type="text"]::placeholder,
  textarea::placeholder{
    font: 33px/37px $OSRegular,Arial,Helvetica,sans-serif;
    color: #a1a1a1;
  }
  input[type="text"]::-moz-placeholder,
  textarea::-moz-placeholder{
    font: 33px/37px $OSRegular,Arial,Helvetica,sans-serif;
    color: #a1a1a1;
  }
  input[type="text"]::-webkit-input-placeholder,
  textarea::-webkit-input-placeholder{
    font: 33px/37px $OSRegular,Arial,Helvetica,sans-serif;
    color: #a1a1a1;
  }
  input[type="text"]:-ms-input-placeholder,
  textarea:-ms-input-placeholder{
    font: 33px/37px $OSRegular,Arial,Helvetica,sans-serif;
    color: #a1a1a1;
  }
  input[type="text"]::-ms-input-placeholder,
  textarea::-ms-input-placeholder{
    font: 33px/37px $OSRegular,Arial,Helvetica,sans-serif;
    color: #a1a1a1;
  }
  input[type="submit"]{
    font: 25px/56px $OSRegular,Arial,Helvetica,sans-serif;
    color: #72bc66;
    text-transform: uppercase;
    opacity: 0.9;
    background-color: white;
    border: 4px solid #74bd34;
    border-radius: 7px;
    box-shadow: 0 3px 5px rgba(0, 0, 0, 0.24);
    -webkit-transition: all 0.4s;
    -moz-transition: all 0.4s;
    -ms-transition: all 0.4s;
    -o-transition: all 0.4s;
    transition: all 0.4s;
    &:hover{
      background: #74bd34;
      color: #ffffff;
    }
  }
}
.footer-wrap{
  background-image: url(../img/donate-fon-block.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.footer{
  padding: 85px 152px 164px 157px;
  background-color: rgba( 0, 0, 0, 0.6);
}
.footer__address,
.news-box,
.associations-box{
  display: inline-block;
  float: left;
}
.footer__address{
  max-width: 400px;
  margin: 0 80px 0 0;
}
.footer__address_theme{
  font: 22px/25px $OSRegular,Arial,Helvetica,sans-serif;
  color: #ffffff;
  a{
    font: 22px/25px $OSRegular,Arial,Helvetica,sans-serif;
    color: #ffffff;
    text-decoration: none;
    &:hover{
      text-decoration: underline;
    }
  }
  h1{
    font: 83px/99px $OSRegular,Arial,Helvetica,sans-serif;
    color: $first_title_h1;
    margin: 0;
  }
  h2{
    a{
      font: 50px/55px $OSRegular,Arial,Helvetica,sans-serif;
    }
    margin: 0 0 20px 0;
  }
  p{
    margin: 0 0 40px 0;
  }
  .address{
    position: relative;
    padding: 40px 0 10px 45px;
    width: 100%;
    .my_g{
      top: 50px;
    }
    .my_h{
      top: 115px;
    }
    .my_i{
      top: 178px;
    }
    address{
      font-style: normal;
    }
    address,
    span,
    a{
      display: block;
      margin: 0 0 40px 0;
    }
  }
  .address_theme{
    border-top: 1px solid #7FC64C;
    border-bottom: 1px solid #7FC64C;
    i{
      position: absolute;
      left: 0;
      font-size: 8px;
      color: #7fc64c;
    }
  }
}
.associations-box{
  h1{
    font: 38px/67px $OSRegular,Arial,Helvetica,sans-serif;
    color: $carousel_link_color;
    text-transform: uppercase;
  }
  margin: 39px 65px 0 0;
}
.associations-box__navigation{
  padding: 20px 0 0 0;
  height: 380px;
  width: 566px;
  nav{
    ul{
      margin: 0;
      padding: 0;
      list-style: none;
      li{
        display: inline-block;
        float: left;
        width: 50%;
        line-height: 0;
        a{
          font: 22px/67px $OSRegular,Arial,Helvetica,sans-serif;
          color: #ffffff;
          text-decoration: none;
          display: inline-block;
        }
      }
    }
  }
}
.associations-box__navigation_theme{
  border-top: 1px solid #7FC64C;
  border-bottom: 1px solid #7FC64C;
}
.news-box{
  width: 500px;
  height: 380px;
  margin: 39px 0 0 0;
  h1{
    font: 38px/67px $OSRegular,Arial,Helvetica,sans-serif;
    color: $carousel_link_color;
    text-transform: uppercase;
  }
}
.news-box__imgs{
  border-top: 1px solid #7FC64C;
  padding: 15px 0 0 0;
  ul{
    width: 490px;
    height: 394px;
    margin: 0;
    padding: 0;
