/*==========  Desktop First Method  ==========*/

/* Large Devices, Wide Screens */
@media only screen and (max-width : 1919px) {
  .header-container{
    padding: 19px 40px 21px 0;
  }
  #nav{
    margin: 10px 0 0 0;
    ul{
      li{
        a{
          padding: 10px 6px;
        }
      }
    }
  }
  .button_donate {
    margin-left: 45px;
  }
  .outer__slider-title-box {
    width: 435px;
    margin: -192px auto 0 auto;
    h1{
      font: 81px/89px "OpenSansRegular",Arial,Helvetica,sans-serif;
      margin: 0 0 10px 0;
    }
    div{
      padding: 10px 72px 20px 72px;
      margin-bottom: 6px;
      h2{
        font: 50px/55px "OpenSansRegular",Arial,Helvetica,sans-serif;
      }
    }
  }
  #thumbs{
    .item{
      //width: auto !important;
      //min-height: auto !important;
      height: 280px;
      h2{
        font: 31.67px/40px "OpenSansRegular",Arial,Helvetica,sans-serif;
      }
    }
    .current{
      .item{
        height: 300px;
        h1 {
          font: 20px/22px "OpenSansRegular",Arial,Helvetica,sans-serif;
        }
        h2{
          font: 35px/45px "OpenSansRegular",Arial,Helvetica,sans-serif;
        }
      }
    }
    .owl-nav{
      .fa{
        font-size: 3em;
      }
    }
  }
  .description-member__box{
    margin: 10px 0 0 0;
    font: 16px/29px "OpenSansRegular",Arial,Helvetica,sans-serif;
    h1 {
      font: 20px/30px "OpenSansRegular",Arial,Helvetica,sans-serif;
      margin: 0 0 20px 0;
    }
    b{
      font: 28px/35px "OpenSansRegular",Arial,Helvetica,sans-serif;
    }
    a{
      font: 20px/45px "OpenSansRegular",Arial,Helvetica,sans-serif;
      margin: 0 20px 15px 0;
    }
    p{
      margin: 0 0 26px 0;
    }
  }
  .description-meet{
    padding: 100px 50px 75px;
    h1{
      font: 40px/50px "OpenSansRegular",Arial,Helvetica,sans-serif;
    }
  }
  .description-meet__box{
    max-width: 430px;
    margin: 0 0 70px 0;
  }
  .meet__box__icon-box{
    margin-right: 15px;
  }
  .meet__box__text-box{
    font: 15px/30px "OpenSansRegular",Arial,Helvetica,sans-serif;
    h2{
      font: 20px/28px "OpenSansRegular",Arial,Helvetica,sans-serif;
    }
  }
  .description-life_box{
    &:before,
    &:after{
      width: 350px;
      left: 310px;
    }
  }
  .description-life_box__theme{
    font: 29px/40px "OpenSansRegular",Arial,Helvetica,sans-serif;
    h1{
      font: 64px/78px "OpenSansRegular",Arial,Helvetica,sans-serif;
      margin: 0 0 30px 0;
    }
    P{
      margin-bottom: 95px;
    }
  }
  .calendar-title{
    padding: 105px 0;
  }
  .calendar-container{
    background: none;
    padding: 0 10px;
    img {
      margin: 150px 30px 60px 25px;
      max-width: 50%;
      height: auto;
    }
  }
  .calendar__content{
    max-width: 400px;
  }
  .calendar__content_theme{
    .theme_button-donate {
      font: 25px/56px "OpenSansRegular",Arial,Helvetica,sans-serif;
      padding: 0 25px;
      margin: 0 0 50px 0;
    }
  }
  .statistic-wrap{
    padding: 170px 0 200px 0;
  }
  .form-block__box-content{
    padding: 45px 80px 52px 320px;
  }
  .box-content_theme{
    h1 {
      font: 55px/67px "OpenSansRegular",Arial,Helvetica,sans-serif;
    }
    span{
      font: 55px/67px "OpenSansRegular",Arial,Helvetica,sans-serif;
    }
  }
  #form{
    padding: 25px 0 0 20px;
    input[type="text"]{
      width: 365px !important;
      height: 48px !important;
      padding: 10px 10px 5px 20px;
      margin: 0 0 15px 0;
    }
    textarea{
      width: 365px !important;
      height: 125px !important;
      margin: 0 0 12px 0;
    }
    input[type="submit"]{
      margin: 0 0 0 205px;
    }
  }
  .form_theme{
    input[type="text"],
    textarea{
      font: 18px/26px "OpenSansRegular",Arial,Helvetica,sans-serif;
    }
    input[type="text"]::placeholder,
    textarea::placeholder{
      font: 20px/26px "OpenSansRegular",Arial,Helvetica,sans-serif;
      color: #a1a1a1;
    }
    input[type="text"]::-moz-placeholder,
    textarea::-moz-placeholder{
      font: 20px/26px "OpenSansRegular",Arial,Helvetica,sans-serif;
      color: #a1a1a1;
    }
    input[type="text"]::-webkit-input-placeholder,
    textarea::-webkit-input-placeholder{
      font: 20px/26px "OpenSansRegular",Arial,Helvetica,sans-serif;
      color: #a1a1a1;
    }
    input[type="text"]:-ms-input-placeholder,
    textarea:-ms-input-placeholder{
      font: 20px/26px "OpenSansRegular",Arial,Helvetica,sans-serif;
      color: #a1a1a1;
    }
    input[type="text"]::-ms-input-placeholder,
    textarea::-ms-input-placeholder{
      font: 20px/26px "OpenSansRegular",Arial,Helvetica,sans-serif;
      color: #a1a1a1;
    }
    input[type="submit"]{
      font: 18px/46px "OpenSansRegular",Arial,Helvetica,sans-serif;
      &:hover{
        background: #74bd34;
        color: #ffffff;
      }
    }
  }
  .footer{
    padding: 85px 20px 164px 20px;
  }
  .footer__address{
       max-width: 370px;
  }
  .footer__address_theme{
    font: 16px/25px "OpenSansRegular",Arial,Helvetica,sans-serif;
    h1 {
      font: 63px/79px "OpenSansRegular",Arial,Helvetica,sans-serif;
    }
    h2{
      a{
        font: 40px/55px "OpenSansRegular",Arial,Helvetica,sans-serif;
      }
    }
  }
  .associations-box{
    margin: 0;
  }
  .associations-box__navigation{
    height: 373px;
    width: 475px;
  }
  .news-box{
    width: 100%;
    height: auto;
    text-align: center;
  }
  .news-box__imgs{
    ul{
      width: auto;
      height: auto;
      flex-flow: row wrap;
      justify-content: space-between;
      align-items: center;
      align-content: center;
    }
  }
}

